.projectsBlock {
    min-height: 100vh;
    background-color: #7bc8bc;
}

.projectContainer {
    align-items: center;
    flex-direction: column;
    justify-content: flex-start;
}

.title {
    background-color: #29918d;
}

.projects {
    box-sizing: border-box;
    border: 1px solid;
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;

}

